<template>
  <div>
    <section v-if="!scanMode" class="main__page">
      <h1>Оставить отзыв об обеде</h1>
      <img src="@/assets/images/onsoup_rating.svg" alt />
      <button v-on:click="startScan">Сканировать новый QR-код</button>
    </section>

    <section v-else class="scanner__wrapper">
      <div class="video__wrapper">
        <video ref="video"></video>
        <div class="message" v-if="status === 'canStart'">Можно начинать</div>
        <div class="message" v-else-if="status === 'Error'">
          Произошла какая-то ошибка
        </div>
      </div>

      <button v-on:click="startScan">Сканировать новый QR-код</button>
      <button v-on:click="stopScan">Остановить сканирование</button>

      <div>Результат: {{ this.res }}</div>
    </section>
  </div>
</template>

<script>
import QrScanner from "qr-scanner";

export default {
  data() {
    return {
      qrScanner: {},
      status: "canStart",
      res: "",
      scanMode: true,
    };
  },
  mounted() {
    this.qrScanner = new QrScanner(
      this.$refs.video,
      (result) => {
        this.res = result.data;
      },
      {
        highlightScanRegion: true,
        highlightCodeOutline: true,
        returnDetailedScanResult: true,
        /* your options or returnDetailedScanResult: true if you're not specifying any other options */
      }
    );
  },
  methods: {
    stopScan() {
      this.qrScanner.pause().then(() => {
        if (this.status !== "Error") {
          this.status = "canStart";
        }
      });
      this.qrScanner._active = false;
    },
    startScan() {
      this.scanMode = true;
      this.qrScanner
        .start()
        .then(() => {
          this.status = "";
        })
        .catch((e) => {
          this.status = "Error";
          console.log(e, "err");
        });
    },
  },
};
</script>

<style>
video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: absolute;
}

button {
  width: 60%;
  height: 35px;
  margin: auto;
  margin-bottom: 15px;
}

.scanner__wrapper {
  font-weight: 600;
}

.video__wrapper {
  box-sizing: content-box;
  margin: 0 1rem;
  margin-bottom: 1.5rem;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70vw;
}

.message {
  position: absolute;
}
</style>
