import Vue from 'vue';
import Router from 'vue-router'
import FeedbackPage from '@/pages/FeedbackPage.vue';
import CodeScanPage from '@/pages/CodeScanPage.vue';

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes:[
    {
      path: '/',
      component: FeedbackPage
    },
    {
      path: '/scan',
      component: CodeScanPage
    }
  ]
})